/* eslint-disable no-shadow */
/* eslint-disable camelcase */
/* eslint-disable no-param-reassign */
/* eslint-disable no-unused-vars */
import { firestoreAction } from 'vuexfire';
import firebase from '@/firebase';
import db from '@/db';

let User;
const posts = db.collection('posts');

firebase.auth().onAuthStateChanged((user) => {
  if (user) User = user.uid;
});

const state = {
  blogs: {},
  posts: [],
  upvoted: false,
};

const getters = {
  blog: (state) => (state.blogs[0] ? state.blogs[0] : {}),
};

const actions = {
  async createPost({ getters }, post) {
    const result = posts.doc();
    post.id = result.id;
    post.vote = [];
    post.approval = true;
    post.blog_id = getters.blog.id;
    post.user_id = firebase.auth().currentUser.uid;
    post.created_at = firebase.firestore.FieldValue.serverTimestamp();
    post.updated_at = firebase.firestore.FieldValue.serverTimestamp();

    try {
      await posts.doc(post.id).set(post);
    } catch (error) {
      console.error(error);
    }
  },
  initBlog: firestoreAction(({ bindFirestoreRef }, name) => {
    bindFirestoreRef('blogs', db.collection('blogs').where('name', '==', name));
  }),
  initPost: firestoreAction(({ bindFirestoreRef }, blog_id) => {
    bindFirestoreRef('posts', posts.where('blog_id', '==', blog_id).orderBy('created_at')
      .where('approval', '==', true));
  }),
  async deletePost(_, post_id) {
    await posts.doc(post_id).delete();
  },
};

export default {
  namespaced: true,
  state,
  actions,
  getters,
  posts,
};
