/* eslint-disable no-undef */
<template>
  <section>
    <div class="blog-type">
      <h1 class="title">{{ blog.name }}</h1>
      <small>{{blog.description}}</small>
    </div>
    <button @click="showForm = !showForm" v-if="isLoggedIn" class="button submit">
      <div>Create a post</div>
      <b-icon pack="fa" icon="plus-circle" size="is-medium"> </b-icon>
    </button>

    <form class="search_bar">
      <b-input class="search_input" placeholder="Search" v-model="searchTerm"></b-input>
    </form>
    <form v-if="showForm && isLoggedIn" class="post-form" @submit.prevent="onCreatePost(post)">
      <b-field label="Title:">
        <b-input placeholder="Title" v-model="post.title" required></b-input>
      </b-field>
      <b-field label="Description:">
        <b-input type="textarea" placeholder="Description" v-model="post.description" required>
        </b-input>
      </b-field>
      <b-field label="If your post includes any codes, please paste them here. (Optional)">
        <b-input type="textarea" v-model="post.codes">
        </b-input>
      </b-field>
      <b-field label="Image URL/ links (optional):">
        <b-input placeholder="URL" type="url" v-model="post.URL"></b-input>
      </b-field>
      <button class="button submit">Submit</button>
    </form>

    <div class="posts">
     <div class="card is-4" v-for="(post, index) in filteredPosts.slice().reverse()"
      :key="post.id">
        <div class="card-image" v-if="isImage(post.URL)">
          <figure class="posted-image">
            <img :src="post.URL" alt="Placeholder image" />
          </figure>
        </div><!--card-image-->
        <div class="card-content">
          <div class="media">
            <div class="media-left">
              <figure class="image is-48x48">
                <img :src="loadedUsersById[post.user_id].image" alt="Avatar" />
              </figure>
            </div><!--media-left-->
            <div class="media_content">
              <p class="title is-4" v-if="!post.URL">{{ post.title }}</p>
              <p class="title is-4" v-if="post.URL">
                <a :href="post.URL" target="_blank">{{ post.title }}</a>
              </p>
              <p class="subtitle is-6">
                {{ loadedUsersById[post.user_id].name }}
                <br />
                <time>{{ getCreated(index) }}</time>
              </p>
            </div><!--media-content-->
          </div><!--media-->

          <pre class="content">
            {{ post.description }}
            <br />
          </pre><!--content-->
        </div><!--card-content-->
        <div class="view">
          <router-link
            :to="{
              name: 'post',
              params: {
                name: $route.params.name,
                post_id: post.id
              }
            }"
            class="button view-post submit"
            >View Post</router-link
          >
          <button
            v-if="user && user.id == post.user_id ||'4aRnFohAFYQicirBV3Aa15rkKry2'"
            @click="onDisplayDelConf(post.id)"
            class="button view-post is-dark">
            Delete
          </button>
          <div v-if="selectedPost == post.id && displayDeleteConfirm" class="fixed delete-confirm">
            <h1 class="title is-5">Are you sure you want to delete your post?</h1>
            <div>
              <button class="button yes" @click="onDeletePost(post.id)">
                Yes
              </button>
              <button class="button is-dark" @click="displayDeleteConfirm = !displayDeleteConfirm">
                No
              </button>
            </div><!-- -->
          </div><!--delete-confirm-->

        </div><!--view-->
      </div> <!--loop-->
    </div>

    <b-notification
      type="is-danger is-light"
      v-model="isActive"
      class="position-fixed"
      aria-close-label="Close notification"
    >
      Your post has been submitted.
    </b-notification>
  </section>
</template>

<script>
import { mapState, mapActions, mapGetters } from 'vuex';

export default {
  data: () => ({
    displayDeleteConfirm: false,
    selectedPost: '',
    showForm: false,
    searchTerm: '',
    isActive: false,
    post: {
      title: '',
      description: '',
      codes: '',
      URL: '',
    },
  }),
  mounted() {
    this.initUsers();
    this.initBlog(this.$route.params.name);
  },
  watch: {
    // eslint-disable-next-line space-before-function-paren
    // eslint-disable-next-line object-shorthand
    '$route.params.name'() {
      this.initBlog(this.$route.params.name);
    },
    blog() {
      if (this.blog.id) {
        this.initPost(this.blog.id);
      }
    },
  },
  computed: {
    ...mapState('blog', ['posts']),
    ...mapState('auth', ['isLoggedIn', 'user']),
    ...mapGetters({
      blog: 'blog/blog',
      usersById: 'users/usersById',
    }),
    loadedUsersById() {
      return this.posts.reduce((byId, post) => {
        // eslint-disable-next-line no-param-reassign
        byId[post.user_id] = this.usersById[post.user_id] || {
          name: 'Loading...',
          image: 'https://bulma.io/images/placeholders/48x48.png',
        };
        return byId;
      }, {});
    },
    filteredPosts() {
      if (this.searchTerm) {
        const regexp = new RegExp(this.searchTerm, 'gi');
        return this.posts.filter((post) => (post.title + post.description).match(regexp));
      }
      return this.posts;
    },
  },
  methods: {
    ...mapActions('blog', ['createPost', 'initBlog', 'deletePost', 'initPost']),
    ...mapActions('users', { initUsers: 'init' }),
    isImage(url) {
      return url.match(/(png|gif|jpg|jpeg)$/);
    },
    async onCreatePost() {
      if (this.post.title && this.post.description) {
        this.createPost(this.post);
        this.post = {
          title: '',
          description: '',
          URL: '',
        };
        this.showForm = false;
        this.isActive = !this.isActive;
      }
    },
    onDisplayDelConf(id) {
      this.displayDeleteConfirm = !this.displayDeleteConfirm;
      this.selectedPost = id;
    },
    async onDeletePost(post_id) {
      this.displayDeleteConfirm = !this.displayDeleteConfirm;
      await this.deletePost(post_id);
    },
    getCreated(index) {
      function timeSince(date) {
        const seconds = Math.floor((new Date() - date) / 1000);
        let interval = Math.floor(seconds / 31536000);
        if (interval > 1) {
          return `${interval} years`;
        }
        interval = Math.floor(seconds / 2592000);
        if (interval > 1) {
          return `${interval} months`;
        }
        interval = Math.floor(seconds / 86400);
        if (interval > 1) {
          return `${interval} days`;
        }
        interval = Math.floor(seconds / 3600);
        if (interval > 1) {
          return `${interval} hours`;
        }
        interval = Math.floor(seconds / 60);
        if (interval > 1) {
          return `${interval} minutes`;
        }
        return `${Math.floor(seconds || 0)} seconds`;
      }
      const posts = this.posts.slice().reverse();
      return timeSince(posts[index].created_at.seconds * 1000) < 0
        ? '0 seconds ago'
        : `${timeSince(posts[index].created_at.seconds * 1000)} ago`;
    },
  },
};
</script>

